import * as React from 'react';
import { Button } from 'semantic-ui-react';
import ActionButtonHoc from '@music/hocs/ActionButtonHoc';
import { IMix } from '@music/interfaces';
import { IButtonVariant } from '@music/components/stateless/PlayButtonVariants/ButtonVariant1';

function ButtonVariant2({ action, isPlaying, isLoading, mix = {} as IMix }: IButtonVariant) {
  return (
    <>
      <Button
        circular
        size="huge"
        className="dark-gray"
        onClick={() => action()}
        loading={!isPlaying && isLoading}
        icon={(isPlaying ? 'pause' : 'play')}
        style={{
          border: isPlaying ? 'solid 1px #0d8b9c' : 'solid 1px #4e4e4e'
        }}
      />
    </>
  );
}

export default ActionButtonHoc(ButtonVariant2);



