export function red(ctx: any) {
  const grd = ctx.createLinearGradient(0, 0, 0, 80);
  grd.addColorStop(0, "#fd5858");
  grd.addColorStop(.5, "#f75151");
  grd.addColorStop(1, "#f31c1c");
  return grd;
}

export function  green(ctx: any) {
  const grd = ctx.createLinearGradient(0, 0, 0, 80);
  grd.addColorStop(0, "#5df5ff");
  grd.addColorStop(.5, "#34edf9");
  grd.addColorStop(1, "#08e6f5");
  return grd;
}



