import * as React from "react";
import { IKeyValueObject } from '@music/interfaces';
import Pipe from '@music/_services/pipe';
import { green } from '@music/helpers/grandients';

interface IFakeVisualizerProps {
  elapsed?: number;
  percent?: number;
  duration?: number;
  unit: "percent" | "second";
  onChange?: (e: React.MouseEvent, percent: number) => any;
}

interface IPipe {
  height: number;
  color: any;
}

export default class FakeVisualizer extends React.Component<
  IFakeVisualizerProps,
  any
> {
  public state = {
    percent: 0,
    lastRefresh: 1
  };
  public el: HTMLDivElement|null;
  public ctx: any;
  private pipes: any[];
  private currentPipeCount: number = 0;
  private canvas: HTMLCanvasElement;
  private readonly PIPE_WIDTH: number = 3;
  private readonly PIPE_HOVER_SPACE: number = 2;

  constructor(props: IFakeVisualizerProps) {
    super(props);
  }

  private get width(): number {
    return this.el ? ((this.el as HTMLDivElement).offsetWidth || 0) : 0;
  }

  private get onChange(): CallableFunction {
    return this.props.onChange || ((e: any, index: number) => null);
  }

  public componentWillUnmount() {
    window.onresize = null;
    this.canvas.onmouseout = null;
    this.canvas.onmousemove = null;
    this.canvas.removeEventListener('click', this.clickHandler);
  }

  public async componentDidMount() {
    try {
      this.canvas = document.getElementById('visualizer') as HTMLCanvasElement;
      this.ctx = this.canvas.getContext('2d');
      this.pipes = this.pipeAllocation();

      this.canvas.addEventListener('click', this.clickHandler);
      this.canvas.onmouseout = (e) => this.makePipe(this.canvas, {...e, layerX: 0, layerY: 0} as MouseEvent)
      this.canvas.onmousemove = this.makePipe.bind(this, this.canvas);
      window.onresize = this.makePipe.bind(this, this.canvas);
      this.setState({...this.props}, () => this.draw(this.updatePipes()));
    } catch (e) {
      console.error(e);
    }
  }

  public async componentDidUpdate(prevProps: IFakeVisualizerProps) {
    const percent = (this.props.percent || 0);
    const lastRefresh = (this.state.lastRefresh || 0);

    console.log('componentDidUpdate', prevProps, this.props.percent);
    // 1. When percent lower than lastRefresh: Mix play again.
    // 2. When subtracted lastRefresh from percent and the result is greater than 0.3.
    if (percent < lastRefresh || (percent - lastRefresh > .3)) {
      this.currentPipeCount = (this.pipes.length / 100) * (this.props.percent || 0);
      this.setState({
        ...this.props,
        lastRefresh: this.props.percent,
      }, () => this.makePipe(this.canvas));
    }
  }

  public draw(pipes: IKeyValueObject[]) {
    const pipeObject = new Pipe;
    pipeObject.setContext(this.ctx);

    pipes.map((pipe: any, index: number) => {
      pipeObject.setX(pipe.x);
      pipeObject.setY(pipe.y);
      pipeObject.setWidth(pipe.width);
      pipeObject.setHeight(pipe.height);
      pipeObject.setFillStyle(pipe.color);
      pipeObject.draw();
    });
  }

  public render() {
    return (
      <>
        <div className="FakeVisualizer" ref={e => this.el = e}>
          <canvas id="visualizer" height="100" width={this.width} />
        </div>
      </>
    );
  }

  private isIntersect(x: number, y: number, xp: number, yp: number, width: number, height: number): boolean {
    return (xp > x && xp < x + this.PIPE_WIDTH) && (yp > y && yp < y + height);
  }

  private isPassed(xp: number, x: number, count: number, index: number): boolean {
    return (xp >= x) || (count >= index);
  }

  private updatePipes(e?: any): IPipe[] {
    let i = this.PIPE_WIDTH;
    const pipeWidth = this.PIPE_WIDTH;
    const event = e || {} as MouseEvent;
    const space = this.PIPE_HOVER_SPACE;

    return this.pipes.map((pipe, index) => {
      let y = (100 - pipe.height), height = pipe.height;
      const xp = (event.layerX || 0);
      const yp = (event.layerY || 0);
      const color = (this.isPassed(xp, i, this.currentPipeCount, (index + 1))) ? '#20cce3' : pipe.color;

      if (e && this.isIntersect(i, y, xp, yp, pipeWidth, height)) {
        y -= space;
        height += space;
      }

      i += pipeWidth;

      return {
        ...pipe,
        height,
        color,
        y,
      }
    });
  }

  private pipeAllocation() {
    let i = 0;
    const dividing = this.PIPE_WIDTH + 1.5;
    const pipesContainerWidth = Math.floor(this.width / dividing);
    return Array(Math.floor(pipesContainerWidth + (pipesContainerWidth / (dividing / 2.25))))
      .fill(1)
      .map(() => {
        const height = 55 + Math.ceil(Math.random() * 25), color = green(this.ctx), y = (100 - height);
        i += this.PIPE_WIDTH;
        return {
          x: i,
          y,
          width: 2.5,
          height,
          color
        };
      });
  }

  private makePipe = (canvas: HTMLCanvasElement, e?: MouseEvent) => {
    this.ctx.clearRect(0, 0, canvas.width, canvas.height);
    const pipes = this.updatePipes(e);
    this.draw(pipes);
  };

  private clickHandler = (e: any) => {
    const index = Math.floor(e.layerX / 3);
    this.currentPipeCount = index;
    this.onChange(e, (index / this.pipes.length ) * 100);
    this.makePipe(this.canvas, e);
  };
}



