import * as React from 'react';
import { connect } from 'react-redux';
import { Segment, Header, Container, Image, Grid, GridColumn } from 'semantic-ui-react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { DiscussionEmbed } from 'disqus-react';
import { has, isMobile, get, timeFormat } from '@music/utils';
import { prepareMix } from '@music/actions/mix';
import SidebarComponent from '../../layouts/Sidebar';
import MixDetailHoc from '@music/hocs/mix/DetailHoc';
import EntityCollection from '@music/extensions/entity-collection';
import TrackList from '@music/components/stateless/TrackList';
import { IState, IKeyValueObject, ISeries, IMix, ITrackCue, MainMixType, IGenre } from '@music/interfaces';
import { setMainMix } from '@music/actions/player';
import { IQuickPlayHoc } from '@music/hocs/QuickPlayHoc';
import gaService from '@music/_services/gaService';
import { GenreLabelList } from '@music/components/stateless/GenreLabel';
import FakeVisualizer from '@music/components/FakeVisualizer';
import PlayerTimer from '@music/components/player/PlayerTimer';
import playerService from '@music/_services/playerService';
import PlayButtonVariant2 from '@music/components/stateless/PlayButtonVariants/ButtonVariant2';
import container from '@music/_services/container';

type MixDetailFullProps = IMixDetailProps & RouteComponentProps & IQuickPlayHoc;

interface IMixDetailProps {
  player: MainMixType;
  series: EntityCollection;
  genres: IGenre[];
  container: IKeyValueObject[];
  currentTrack: ITrackCue|undefined;
  prepareMix: (mix: IMix, addToContainer: boolean, cb: (mix: IMix) => any) => any;
  setMainMix: (mix: MainMixType) => any;
}

interface IMixDetailState {
  isTimerAvailable: boolean;
  currentSeries: ISeries|null|undefined;
  mix: IMix
}

class MixDetail extends React.Component<MixDetailFullProps, IMixDetailState> {
  public state: Readonly<IMixDetailState> = {
    currentSeries: null,
    mix: {} as IMix,
    isTimerAvailable: false
  };

  constructor(props: MixDetailFullProps) {
    super(props);
  }

  get container(): EntityCollection {
    return new EntityCollection(this.props.container);
  }

  get mix(): IMix|null {
    return this.state.mix;
  }

  get current(): IKeyValueObject {
    return this.props.currentTrack || {};
  }

  get trackName(): string {
    return get('track.name', this.current, '-');
  }

  get orderNumber(): string {
    return get('orderNumber', this.current);
  }

  get imageUrl(): string {
    return get('track.source.imageUrl', this.current, '');
  }

  get isCurrentMix(): boolean {
    return !!this.mix && get('mix.id', this.props.player) === this.mix.id;
  }

  get isPlaying(): boolean {
    return this.props.player.playing;
  }

  get part(): IKeyValueObject {
    const qs = new URLSearchParams(this.props.location.search)
    return qs.has('part') ? {part: parseInt(qs.get('part') || '0')} : {};
  }

  public componentWillMount() {
    this.props.setMainMix({...this.props.player, visible: false});
  }

  public componentWillUnmount() {
    this.setState({ isTimerAvailable: false });
    this.props.setMainMix({...this.props.player, visible: true});
  }

  public componentDidMount() {
    const { slug, count } = this.props.match.params as IKeyValueObject;
    const currentSeries = this.props.series.find(slug, 'slug') as ISeries;
    const where = {seriesId: currentSeries.id, count: parseInt(count), ...this.part};
    const mix = this.container.where({...where}).first() as IMix;
    console.log('this.props.player', this.props);
    this.setState({
        currentSeries,
        mix,
        isTimerAvailable: this.props.player.playing
      }, () => gaService.pageView(`Mix Detail: ${currentSeries.name} #${count}`)
    );
  }

  public componentDidUpdate(prevProps: MixDetailFullProps) {
    let newState = {};
    const { count } = this.props.match.params as IKeyValueObject;
    const series = this.state.currentSeries || {} as IMix;
    const where = {seriesId: series.id, count: parseInt(count), ...this.part};
    const mix = this.container.where(where).first() as IMix;

    if (mix && !has('cues.0.track', this.state.mix)) {
      this.props.prepareMix({...mix}, true, (prepared) => {
        this.setState({mix: prepared });
      });
    }
    if (this.props.player.playing !== prevProps.player.playing) {
      newState = {
        ...newState,
        isTimerAvailable: this.props.player.playing
      }
    }
    if (Object.keys(newState).length > 0) {
      this.setState({...newState});
    }
  }

  public render() {
    return !this.state.currentSeries  ? null : (
      <div id="mix-detail-container">
        <SidebarComponent>
          {(!!this.mix && this.mix.count) &&
            <Segment style={{backgroundImage: `url(${this.mix.cover || '/pictures/cover-bg.jpeg'})`, height: isMobile() ? '100vh' : '500px'}} className="wide-cover shake-bg">
              <>
                <Segment basic>
                  <Header className="header head-text-bg" as="h1">{this.state.currentSeries.name} #{this.mix.count}</Header>
                  {Boolean(this.mix.description) &&
                    <>
                      <div style={{width: 1, height: 13}} />
                      <Header className="head-text-bg" as="h3">{this.mix.description}</Header>
                    </>
                  }
                  <div className="genre-list">
                    <GenreLabelList
                      genres={this.props.genres}
                      mixGenres={this.mix.genres || []}
                      labelProps={{basic: false, size: 'medium'}}
                    />
                  </div>
                  <div className="play-button">
                    <PlayButtonVariant2
                      {...this.props}
                      mix={{
                        ...this.mix,
                        series: this.props.series.first()
                      }}
                      service={container.get('mainPlayer')}
                    />
                  </div>
                  {(this.trackName.length > 1 && this.isCurrentMix) &&
                    <Grid style={{marginTop: '10px', marginLeft: 0}}>
                      <Grid.Row columns="1">
                        <GridColumn mobile="16" widescreen="8" largeScreen="8" tablet="10">
                          <Header
                            className="black-bg"
                            as="h4">{this.orderNumber}. {this.trackName}
                          </Header>
                        </GridColumn>
                      </Grid.Row>
                      <Grid.Row columns="1" style={{paddingTop: 0}}>
                        <GridColumn mobile="3" widescreen="1" largeScreen="1" tablet="2">
                          <Image src={this.imageUrl} style={{border: 'solid 1px #4c4c4c', width: '90%'}} />
                        </GridColumn>
                      </Grid.Row>
                    </Grid>
                  }
                </Segment>
                  {(this.isCurrentMix) &&
                    <PlayerTimer playerService={container.get('mainPlayer')} start={this.state.isTimerAvailable} render={(currentTime: number, duration: number) => (
                      <div style={{height: 'auto', width: '100%', position: 'absolute', bottom: 0, right: 0}}>
                        <FakeVisualizer
                          unit="percent"
                          onChange={this.seekHandler}
                          percent={(Math.ceil((currentTime / duration) * 100))}
                        />
                        <div className="time" style={{left: 0}}>{timeFormat(currentTime)}</div>
                        <div className="time" style={{right: 0}}>{timeFormat(duration)}</div>
                      </div>
                    )}>
                    </PlayerTimer>
                  }
                </>
              </Segment>
            }
            <Container>
              {(!!this.mix && this.mix.cues) &&
                <TrackList
                  cues={this.mix.cues}
                  isPlaying={this.props.player.playing}
                  currentTrack={this.props.currentTrack}
                />
              }
            </Container>
            {!!this.state.mix &&
              <div style={{margin: '20px', height: 100}}>
                <DiscussionEmbed shortname="themixzonecom" config={{
                    url: location.href,
                    identifier: this.state.mix.id,
                    title: `${this.state.currentSeries.name} #${this.state.mix.count}`,
                  }}
                />
              </div>
            }
        </SidebarComponent>
      </div>
    );
  }

  private seekHandler = (e: React.MouseEvent, percent: number) => {
    const { duration } = playerService.element();
    playerService.element().currentTime = (duration / 100) * percent;
  };
}

const mapDispatchToProps = (dispatch: CallableFunction) => {
  return {
    setMainMix: (mix: MainMixType) => dispatch(setMainMix({...mix})),
    prepareMix: (mix: IMix, addToContainer: boolean, cb: (mix: IMix) => any) => dispatch(prepareMix(mix, addToContainer, cb)),
  }
};

const mapStateToProps = (state: IState) => {
  return {
    player: state.player,
    container: [...state.mixContainer.data],
    series: new EntityCollection(state.series.data),
    genres: [...state.genres.data],
    currentTrack: Object.keys(state.currentTrack).length > 0 ? {...state.currentTrack} : undefined,
  }
};

const MixDetailPage = withRouter(connect(mapStateToProps, mapDispatchToProps)(MixDetailHoc(MixDetail)) as any);

export default MixDetailPage;



